@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.square {
    overflow:hidden;
    width: 45px;
    height: 45px;
    color:black;
    border:0.5px solid rgb(209, 209, 209);
    padding:5px;
}

.square-filled {
    background-color: rgb(192, 191, 191);
}

.square:hover {
    background-color: #F3F3F3;
}

.square:active {
    background-color: #E3E3E3;
}

.square-clear {
    background-color: #FEFEFE;
}

.square-start {
    background-color: #7BD328;
    cursor: pointer;
}

.square-start:hover {
    background-color: #7BD328;
}

.square-end {
    background-color:#5E9834;
    cursor: pointer;
}

.square-end:hover {
    background-color:#5E9834;
}

.square-path {
    background-color: #F9A71F;
}

.square-path:hover {
    background-color: #F9A71F;
}
.table {
    width: auto;
    height: auto;
    color:white;
    font-size: 3em;
    table-layout: fixed;
    margin:auto;
}
.pannel {
    width: 40%;
    background-color:#F1F1F1;
    border-radius:2px;
    border:1px solid rgb(219, 219, 219);
    margin:10px auto;
    text-align: center;
}
.container {
    color:gray;
    font-size: 15px;
    margin:auto;
}

label {
    float:left;
    padding: 5px;
    text-align: center;
}

.box {
    width: 50px;
    height: 50px;
    border:0.5px solid rgb(209, 209, 209);
    margin:10px;
}

.box-filled {
    background-color: rgb(192, 191, 191);
}

.box-hover {
    background-color: #F3F3F3;
}

.box-down {
    background-color: #E3E3E3;
}

.box-clear {
    background-color: #FEFEFE;
}

.box-start {
    background-color: #7BD328;
}

.box-end {
    background-color:#5E9834;
}

.box-shortest {
    background-color: #F9A71F;
}
.App {
    font-family: 'Roboto', sans-serif;
}
